/**
 * Google ÏÜåÏÖú Î°úÍ∑∏Ïù∏ Íµ¨ÌòÑ
 *
 * Ïù¥ ÌååÏùºÏùÄ Google OAuthÎ•º ÌÜµÌïú Ïù∏Ï¶ù Íµ¨ÌòÑÏùÑ Îã¥ÎãπÌï©ÎãàÎã§.
 */

import { SupabaseClient } from "@supabase/supabase-js";
import { Database } from "@/types/supabase";
import {
  AuthResult,
  OAuthProviderConfig,
  SocialAuthError,
  SocialAuthErrorCode,
  SocialAuthOptions,
} from "./types";

/**
 * Google OAuth ÏÑ§Ï†ï
 */
export function getGoogleConfig(): OAuthProviderConfig {
  return {
    clientId: process.env.NEXT_PUBLIC_GOOGLE_CLIENT_ID || "",
    clientSecretEnvKey: "GOOGLE_CLIENT_SECRET",
    defaultScopes: [
      "email",
      "profile",
      "openid",
    ],
    additionalConfig: {
      // Google ÌäπÌôî ÏÑ§Ï†ï
      accessType: "offline", // Î¶¨ÌîÑÎ†àÏãú ÌÜ†ÌÅ∞ ÏöîÏ≤≠
      prompt: "consent", // Îß§Î≤à ÎèôÏùò ÌôîÎ©¥ ÌëúÏãú (ÌÖåÏä§Ìä∏ Ïãú Ïú†Ïö©)
      includeGrantedScopes: true, // Ïù¥Ï†ÑÏóê ÌóàÏö©Ìïú Í∂åÌïú Ìè¨Ìï®
    },
  };
}

/**
 * Google Î°úÍ∑∏Ïù∏ Íµ¨ÌòÑ
 *
 * @param supabase Supabase ÌÅ¥ÎùºÏù¥Ïñ∏Ìä∏
 * @param options Ïù∏Ï¶ù ÏòµÏÖò
 * @returns Ïù∏Ï¶ù Í≤∞Í≥º
 */
export async function signInWithGoogleImpl(
  supabase: SupabaseClient<Database>,
  options?: SocialAuthOptions,
): Promise<AuthResult> {
  try {
    console.log("üîç signInWithGoogleImpl Ìï®Ïàò ÏãúÏûë");

    // ÏÑ§Ï†ïÍ∞í Ï§ÄÎπÑ
    const config = getGoogleConfig();
    console.log("üîç Google ÏÑ§Ï†ï Î°úÎìú ÏôÑÎ£å:", config);

    // Î¶¨Îã§Ïù¥Î†âÌä∏ URL Í≤∞Ï†ï (ÌôòÍ≤ΩÎ≥ÄÏàò Ïö∞ÏÑ† ÏÇ¨Ïö©)
    let redirectUrl = options?.redirectUrl;
    if (!redirectUrl) {
      // Í∞úÎ∞ú ÌôòÍ≤ΩÏóêÏÑúÎäî ÌôòÍ≤ΩÎ≥ÄÏàò ÎòêÎäî localhost ÏÇ¨Ïö©
      if (process.env.NODE_ENV === "development") {
        const siteUrl = process.env.NEXT_PUBLIC_SITE_URL;
        if (siteUrl) {
          redirectUrl = `${siteUrl}/auth/callback/google`;
        } else if (typeof window !== "undefined") {
          // ÌôòÍ≤ΩÎ≥ÄÏàòÍ∞Ä ÏóÜÏúºÎ©¥ ÌòÑÏû¨ origin ÏÇ¨Ïö©
          redirectUrl = `${window.location.origin}/auth/callback/google`;
        } else {
          redirectUrl = "http://localhost:3100/auth/callback/google";
        }
      } else {
        // ÌîÑÎ°úÎçïÏÖò ÌôòÍ≤Ω
        if (typeof window !== "undefined") {
          redirectUrl = `${window.location.origin}/auth/callback/google`;
        } else {
          redirectUrl = "https://www.picnic.fan/auth/callback/google";
        }
      }
    }

    const scopes = options?.scopes || config.defaultScopes;

    console.log("üîç Google OAuth ÏãúÏûë:", {
      redirectUrl,
      nodeEnv: process.env.NODE_ENV,
      siteUrl: process.env.NEXT_PUBLIC_SITE_URL,
      currentOrigin: typeof window !== "undefined"
        ? window.location.origin
        : "server",
    });

    // Î°úÏª¨ Ïä§ÌÜ†Î¶¨ÏßÄÏóê Î¶¨Îã§Ïù¥Î†âÌä∏ URL Ï†ÄÏû• (ÏΩúÎ∞± ÌõÑ ÎêòÎèåÏïÑÏò¨ ÏúÑÏπò)
    if (typeof localStorage !== "undefined") {
      const returnUrl = options?.additionalParams?.return_url ||
        window.location.pathname;
      localStorage.setItem("auth_return_url", returnUrl);
      console.log("üîç Î°úÏª¨ Ïä§ÌÜ†Î¶¨ÏßÄÏóê return_url Ï†ÄÏû•:", returnUrl);
    }

    // Google ÌäπÌôî Ï∂îÍ∞Ä ÌååÎùºÎØ∏ÌÑ∞
    const googleParams = {
      access_type: (config.additionalConfig as any)?.accessType || "offline",
      prompt: (config.additionalConfig as any)?.prompt || "select_account",
      include_granted_scopes:
        (config.additionalConfig as any)?.includeGrantedScopes
          ? "true"
          : "false",
      ...options?.additionalParams,
    };

    console.log("üîç Google OAuth ÌååÎùºÎØ∏ÌÑ∞:", googleParams);
    console.log("üîç Supabase signInWithOAuth Ìò∏Ï∂ú ÏãúÏûë");

    // Supabase OAuth ÏÇ¨Ïö©
    const { error } = await supabase.auth.signInWithOAuth({
      provider: "google",
      options: {
        redirectTo: redirectUrl,
        scopes: scopes.join(" "),
        queryParams: googleParams,
      },
    });

    console.log("üîç Supabase signInWithOAuth Ìò∏Ï∂ú ÏôÑÎ£å, error:", error);

    if (error) {
      console.error("‚ùå Google OAuth Ïò§Î•ò:", error);
      throw new SocialAuthError(
        SocialAuthErrorCode.AUTH_PROCESS_FAILED,
        `Google Î°úÍ∑∏Ïù∏ ÌîÑÎ°úÏÑ∏Ïä§ Ïã§Ìå®: ${error.message}`,
        "google",
        error,
      );
    }

    console.log("‚úÖ Google OAuth Î¶¨Îã§Ïù¥Î†âÏÖò ÏãúÏûë");

    // OAuth Î¶¨ÎîîÎ†âÏÖòÏúºÎ°ú Ïù∏Ìï¥ Ïù¥ Ìï®ÏàòÎäî Ïó¨Í∏∞ÍπåÏßÄÎßå Ïã§ÌñâÎêòÍ≥† Î¶¨ÎîîÎ†âÏÖòÎê®
    // Î¶¨ÎîîÎ†âÏÖò ÌõÑ ÏΩúÎ∞± Ï≤òÎ¶¨Îäî callback Ìï∏Îì§Îü¨ÏóêÏÑú ÏàòÌñâ
    return {
      success: true,
      provider: "google",
      message: "Google Î°úÍ∑∏Ïù∏ Î¶¨ÎîîÎ†âÏÖò Ï§ë...",
    };
  } catch (error) {
    console.error("üîç signInWithGoogleImpl Ïò§Î•ò:", error);

    if (error instanceof SocialAuthError) {
      throw error;
    }

    throw new SocialAuthError(
      SocialAuthErrorCode.AUTH_PROCESS_FAILED,
      error instanceof Error ? error.message : "Ïïå Ïàò ÏóÜÎäî Google Î°úÍ∑∏Ïù∏ Ïò§Î•ò",
      "google",
      error,
    );
  }
}

/**
 * Google ÌîÑÎ°úÌïÑ Ï†ïÎ≥¥ Ï≤òÎ¶¨
 *
 * @param profile GoogleÏóêÏÑú Î∞òÌôòÎêú ÌîÑÎ°úÌïÑ Ï†ïÎ≥¥
 * @returns ÌëúÏ§ÄÌôîÎêú ÏÇ¨Ïö©Ïûê ÌîÑÎ°úÌïÑ Ï†ïÎ≥¥
 */
export function normalizeGoogleProfile(profile: any): Record<string, any> {
  // Google ID ÌÜ†ÌÅ∞ÏóêÏÑú ÌååÏã±Îêú Ï†ïÎ≥¥ ÎòêÎäî userinfo ÏóîÎìúÌè¨Ïù∏Ìä∏ÏóêÏÑú Í∞ÄÏ†∏Ïò® Ï†ïÎ≥¥
  return {
    id: profile.sub || profile.id,
    name: profile.name || "",
    email: profile.email || "",
    avatar: profile.picture || "",
    verified: profile.email_verified || false,
    familyName: profile.family_name || "",
    givenName: profile.given_name || "",
    locale: profile.locale || "",
    provider: "google",
  };
}

/**
 * Google ID ÌÜ†ÌÅ∞ Í≤ÄÏ¶ù Î∞è ÌååÏã±
 *
 * @param idToken GoogleÏóêÏÑú Î∞òÌôòÎêú ID ÌÜ†ÌÅ∞
 * @returns ÌååÏã±Îêú ÌÜ†ÌÅ∞ ÌéòÏù¥Î°úÎìú
 */
export function parseGoogleIdToken(idToken: string): Record<string, any> {
  try {
    const payload = idToken.split(".")[1];
    const decoded = Buffer.from(payload, "base64").toString("utf8");
    return JSON.parse(decoded);
  } catch (error) {
    console.error("Google ID ÌÜ†ÌÅ∞ ÌååÏã± Ïò§Î•ò:", error);
    return {};
  }
}
